body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', serif;
  font-size: 16px;
  background-color: #F2F8FC;
}

h1,
h2,
h3,
h4,
h5,
h6,
button,
a {
  font-family: 'Cardo', sans-serif;
  text-align: center;
}
/* nav.footer.navbar.fixed-bottom */
nav.footer.navbar{
    background-color: #4B9CD3;
    color: #fff;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.footer-img {
    width: 35px;
    height: 35px;
}

#copyright {
    margin-top: 10px;
}
nav.justify-content-end.navbar {
    background-color: #4B9CD3 !important;
    height: 75px;
}

.navbar-logo {
    margin: 0 auto;
    width: 80px;
    height: 80px;
}

#follow {
    color: white;
    margin: 5px;
}

.navbar-title {
    text-align: center;
}

a.navbar-brand {
    width: 50px;
    height: 50px;
    margin: 2px;
    padding: 0px;
}

.social-img-nav {
    width: 35px;
    height: 35px;
    padding: 3px;
}

@media screen and (max-width: 568px) {
    #follow {
        display: none;
    }
}
#close-btn,
#subscribe-btn {
    float: right;
    margin: 2px;
}

#close-btn {
    background-color: #E63946;
    color: #ffffff;
}

#subscribe-btn {
    background-color: #2E5077;
    color: #ffffff;
}
