ul.ml-auto.navbar-nav,
nav.navbar.navbar-expand-md.navbar-light.bg-light {
    padding: 0;
    background-color: #F2F8FC !important;
    width: 100%;
    margin: 0;
    list-style: none;
    display: flex;
    flex-direction:row;
    justify-content: space-around;
    align-items: center;
}

hr {
    border-top: 1.5px solid rgba(216,216,216);
    margin: 1px;
}

div#nav-header {
    margin-left: 15%;
    margin-right: 15%;
}

.row.navbar-header-content {
    padding: 20px;
}

#title {
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
}

.nav-link {
    font-family: 'Montserrat', sans-serif;
    display: block;
    margin: 30px 5px;
    padding: .3rem 1rem;
}

a.navheader-link.nav-link,
a#collasible-nav-dropdown {
    text-decoration: none;
    margin: 5px;
    color: #000 !important;
}

#seek {
    font-family: 'Cardo', sans-serif;
    font-size: 1.25em;
    display: inline;
}

.navbar-social {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.social-img {
    width: 35px;
    height: 35px;
    margin: 2px;
    color: #4B9CD3;
}

.navbar-title {
    padding: 10px;
}

.navbar-subscribe {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

@media screen and (max-width:568px) {
    .row.navbar-header-content {
        padding: 5px;
    }
    ul.ml-auto.navbar-nav {
        background-color: #F2F8FC;
        width: 100%;
        margin: 0;
        list-style: none;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }
    .nav-link {
        margin: 5px;
    }
    .link-container {
        margin: 10px;
    }
}
.container {
    margin-bottom: 100px;
}

.video-container {
    position: relative;
    overflow: hidden;
    padding-top: 56.25%;
}
div#player,
iframe#widget2,
.video-display {
    /* width: 100%;
    height: 500px;
    margin-bottom: 10px; */
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
}

#home {
    /* min-height: auto; */
    margin-top: 10px;
    padding: 25px;
    background-image: url("https://images.pexels.com/photos/37487/pexels-photo-37487.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
    background-size:cover;
    background-repeat:no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.welcome {
    width: 85%;
    text-align: center;
    color: #fff;
}
/* 
@media screen and (max-width: 568px) {
    #react-player {
        width: 100%;
        height: 200px;
    }
} */
.card {
      width: 265px;
      height: 300px;
      margin: 0 auto;
      /* overflow: hidden; */
}

.card-title {
      font-size: 16px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
}
#main-wrapper {
    padding-right: 0;
}

#main-blog {
    width: 65%;
    height: 75vh;
}

#main-title,
#main-date {
    text-align: left;
}

.blog-container {
    width: 100%;
    margin-top: 25px;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: center;
}

#summary {
    width: 35%;
    float:right;
    text-align: left;
    background-color:#4B9CD3;
    color: #fff;
    /* padding: 45px 80px 45px 45px; */
    padding: 60px;
    height: 75vh;
}

.card-body p {
    width: 80%;
    margin: 0 auto;
}

#main-blog-read {
    background-color:#4B9CD3;
    border: 1px solid #fff;
    color: white;
    margin: 5px;
    font-size: 16px;
}

#main-blog-read:hover {
    background-color:  #3E80AD;
}

.card {
    margin-top: 10px;
    font-size: .85em;
}

.card-body {
    background-color:#4B9CD3;
    color: #fff;
}

@media screen and (max-width:568px) {
    #main-blog,
    #summary,
    .card {
        width: 100%;
        height: auto;
    }
    .card {
        margin-top: 5px;
    }
}
#ring-holder {
    display: flex;
    align-content: center;
    justify-content: center;
}

#blog-container {
    display:none;
}

#blog-image {
    width: 75%;
    height: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

h1#blog_title {
    margin: 15px;
    font-family: 'Montserrat', serif;
}

.quote-hr {
    margin: 20px;
}

.quotes {
    font-size: 25px;
    font-family: 'Libre Baskerville', serif;
}

#quote {
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
}

.social-share {
    display: flex;
    justify-content: center;
    align-content: space-between;
}

.SocialMediaShareButton {
    margin: 3px;
}

audio.react-audio-player {
    margin: 50px auto 25px;
    height: auto;
    display: block;
}

div#blog p,
div#blog li,
div#blog ul {
    width: 75%;
    margin: 0 auto;
    font-family: 'Cardo', sans-serif;
    text-align:left;
}


p#first_para::first-letter {
    color: #E63946;
    float: left;
    font-family: 'Cardo', sans-serif;
    font-size: 75px;
    line-height: 60px;
    padding-top: 10px;
    padding-right: 2%;
    padding-left: 3px;
}

@media screen and (max-width:568px) {
    #blog-image {
        width: 100%;
        height: auto;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    div#blog p {
        width: 100%;
        margin: 0 auto;
        font-family: 'Cardo', sans-serif;
    }
}
.about {
    margin: 0 auto;
    width: 75%;
    text-align: center;
}






.ql-container.ql-snow {
    height: 50vh;
    margin-bottom: 20px;
}

.ql-editor {
    background-color: #ffffff;
}

.editor-wrapper,
.uploader-wrapper {
    padding: 10px;
}

#placeholder {
    width: 350px;
    height: 250px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    /* width: 50%; */
}

input#icon-button-file,
input#icon-button-file-audio,
input#icon-button-file-video {
    display: none;
}

label {
    margin: 15px;
}

.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  overflow: auto;
}

.App-content {
  flex: 1 1;
}

